import React, { useState } from 'react';

import YouTube, { YouTubeProps } from 'react-youtube';
import './style.css';

const Player = ({
  setHasLoaded,
  videoId
}: {
  setHasLoaded: (hasLoaded: boolean) => void;
  videoId: string;
}) => {
  const [number, setNumber] = useState(0);
  console.log(number);

  const _onReady: YouTubeProps['onReady'] = (event) => {
    setHasLoaded(true);
    event.target.playVideo();
  };
  const _onStateChange: YouTubeProps['onStateChange'] = (event) => {
    console.log(event);
    setNumber(event.data);
    alert(event.data);
  };

  //*configuration: https://developers.google.com/youtube/player_parameters *//
  const opts: YouTubeProps['opts'] = {
    playerVars: {
      autoplay: 1,
      rel: 0,
      controls: 2
    }
  };

  const setVideoClass = (caseNumber: number) => {
    switch (caseNumber) {
      case 0:
        return 'videoInner ';
      case 1:
        return 'videoInner videoInner-top';
      case -1:
        return 'videoInner';
      case 3:
        return 'videoInner';
      default:
        return 'videoInner';
    }
  };
  return (
    <YouTube
      videoId={videoId}
      onReady={_onReady}
      onStateChange={_onStateChange}
      className={setVideoClass(number)}
      iframeClassName={setVideoClass(number)}
      opts={opts}
    />
  );
};

export default Player;
